html {
  margin: 0;
  background-image: linear-gradient(45deg, hsl(160, 86%, 45%) 5% ,#c079fc 80%);
  background-repeat: no-repeat;
  background-size: cover;
  font-size: 16px;
}

body{
  height: 100%;
  width: 100%;
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

#root{
  height: 100%;
}


